.projects {
	.project {
		display: relative;
		.title {
			@include flex-container(row, space-between, center, nowrap);
			width: 100%;
			.icon {
				color: var.$text;
			}
		}
		.picture {
			height: 10rem;
		}
		.tag-wrapper {
			padding: 0.5rem 0;
			grid-area: tags;
			@include flex-container(row, flex-start, center, wrap);
			justify-self: start;
			gap: 0.25rem;
			.tag {
				background-color: var.$accent1-default;
				padding: 0.5rem;
				border-radius: 0.25rem;
			}
		}
	}
}
@media only screen and (max-width: var.$screen_M) {
	.projects {
		align-items: flex-start;
		.project {
			margin: 0.5rem auto;
			display: grid;
			grid-template-areas:
				"title"
				"picture"
				"description"
				"tags";
			justify-content: stretch;
			gap: 0.5rem;
			.title {
				justify-self: flex-start;
				grid-area: title;
			}
			.picture {
				grid-area: picture;
				border-radius: 1rem;
				height: 10rem;
				justify-self: center;
				margin: 0 1rem 0 0;
			}
			.description {
				justify-self: flex-start;
				text-align: left;
				grid-area: description;
			}
		}
	}
}
@media only screen and (min-width: var.$screen_M) {
	.projects {
		align-items: flex-start;
		.project {
			margin: 0.5rem auto;
			display: grid;
			grid-template-areas:
				"picture title"
				"picture description"
				"tags tags";
			justify-content: stretch;
			grid-template-columns: min-content 1fr;
			grid-template-rows: auto 1fr;
			gap: 0.5rem;
			.picture {
				grid-area: picture;
				border-radius: 1rem;
				height: 10rem;
				justify-self: flex-end;
				margin: 0 1rem 0 0;
			}
			.title {
				justify-self: flex-start;
				grid-area: title;
			}
			.description {
				justify-self: flex-start;
				text-align: left;
				grid-area: description;
			}
		}
	}
}
