@use "../var";
@import "../mixins";

#landing-page {
	grid-area: main;
	// height: 500vh;
	width: clamp(10rem, 100%, 80rem);

	@import "./brendan";
	@import "./education";
	@import "./projects";
	@import "./_skills";
	display: grid;
	justify-content: stretch;
	align-items: stretch;
}
@media only screen and (min-width: 0px) and (max-width: var.$screen_M) {
	#landing-page {
		padding: 3rem 1rem;
		margin: 0 auto;

		grid-template-areas:
			"landing"
			"projects"
			"skills"
			"education"
			"more-about-me";
		grid-template-rows: repeat(4, min-content);

		.brendan {
			grid-area: landing;
		}
		.projects {
			grid-area: projects;
		}
		.my-education {
			grid-area: education;
		}
		.skills {
			grid-area: skills;
		}

		.more-about-me {
			grid-area: more-about-me;
		}
	}
}
@media only screen and (min-width: var.$screen_M) {
	#landing-page {
		margin: 0 auto;
		padding: 2rem;

		grid-template-areas:
			"landing landing"
			"projects skills"
			"projects education"
			"more-about-me more-about-me";
		grid-template-rows: repeat(4, min-content);

		gap: 1rem;

		.brendan {
			grid-area: landing;
		}
		.projects,
		.my-education {
			h1 {
				padding: 1rem 0;
			}
		}
		.projects {
			grid-area: projects;
		}
		.my-education {
			grid-area: education;
		}

		.my-knowledge {
			grid-area: my-knowledge-btn;
		}
		.my-projects {
			grid-area: my-projects-btn;
		}
		.more-about-me {
			grid-area: more-about-me;
		}
	}
}
