.education {
	h1 {
		padding: 1rem 0;
	}
	.section {
		display: grid;
	}
}

@media only screen and (min-width: 0px) and (max-width: var.$screen_M) {
	.education {
		.section {
			grid-template-areas:
				"stevens-text stevens-logo"
				"gl-text gl-logo";

			gap: 0.5rem;
			.stevens-logo {
				grid-area: stevens-logo;
				height: 10rem;
				padding: 1rem 0;
				margin-right: 1rem;
			}
			.stevens-text {
				grid-area: stevens-text;
				align-self: center;
			}
			.gl-text {
				grid-area: gl-text;
				align-self: center;
			}
			.gl-logo {
				grid-area: gl-logo;
				height: 4rem;
			}
		}
	}
}

@media only screen and (min-width: var.$screen_M) {
	.education {
		.section {
			grid-template-areas:
				"stevens-text stevens-logo"
				"gl-text gl-logo";

			gap: 0.5rem;

			.stevens-logo {
				grid-area: stevens-logo;
				height: 10rem;
				padding: 1rem 0;
				margin-right: 1rem;
			}
			.stevens-text {
				grid-area: stevens-text;
				align-self: center;
			}
			.gl-text {
				grid-area: gl-text;
				align-self: center;
			}
			.gl-logo {
				grid-area: gl-logo;
				height: 4rem;
			}
		}
	}
}
