.skills {
	align-items: flex-start;
	.section {
		display: grid;
		grid-template-areas: "language software";
		gap: 0.5rem;
	}
	.software {
		grid-area: software;
	}
	.language {
		grid-area: language;
	}
	.skill {
		// width: max-content;
		display: grid;
		gap: 0.5rem;
		margin: 0.25rem 0;

		.picture {
			border-radius: 0.25rem;
			grid-area: picture;
			// height: 3rem;
			width: 3rem;
			margin: 0 0.5rem;
		}
		.title {
			justify-self: start;
			grid-area: title;
		}
	}
}
@media only screen and (max-width: var.$screen_M) {
	.skills {
		align-items: flex-start;
		.section {
			display: grid;
			grid-template-areas: "language software";
			gap: 0.5rem;
		}

		.skill {
			grid-template-areas: "picture" "title";
			justify-content: center;
			align-items: center;
			justify-self: center;
			align-self: center;
			grid-template-columns: min-content 1fr;
			grid-template-rows: auto 1fr;
			gap: 0.5rem;
			margin: 0.25rem auto;

			.picture {
				border-radius: 0.25rem;
				grid-area: picture;
				width: 3rem;
				margin: 0 0.5rem;
			}
			.title {
				width: 100%;
				text-align: center;
				justify-self: center;
				grid-area: title;
			}
		}
	}
}
@media only screen and (min-width: var.$screen_M) {
	.skills {
		align-items: flex-start;
		.section {
			display: grid;
			grid-template-areas: "language software";
			gap: 0.5rem;
		}

		.skill {
			// width: max-content;
			display: grid;
			grid-template-areas: "picture title";
			justify-content: flex-start;
			align-items: center;

			grid-template-columns: min-content 1fr;
			grid-template-rows: auto 1fr;
			gap: 0.5rem;
			margin: 0.25rem 0;

			.picture {
				border-radius: 0.25rem;
				grid-area: picture;
				// height: 3rem;
				width: 3rem;
				margin: 0 0.5rem;
			}
			.title {
				justify-self: start;
				text-align: left;
				grid-area: title;
			}
		}
	}
}
