h1 {
	@include text(var.$font-title, var.$XXL, var.$Bold-weight);

	color: var.$text;
}

h2 {
	@include text(var.$font-title, var.$XL, var.$Medium-weight);

	color: var.$text;
}

h3 {
	@include text(var.$font-body, var.$L, var.$Medium-weight);
	color: var.$text;
}

h4 {
	@include text(var.$font-body, var.$M, var.$Medium-weight);
	color: t(var.$text);
}
h5 {
	@include text(var.$font-body, var.$M, var.$Default-weight);
	color: t(var.$text);
}
h6 {
	@include text(var.$font-body, var.$button, var.$Medium-weight);
	color: var.$text;
}

p {
	@include text(var.$font-body, var.$default, var.$Default-weight);
	color: var.$text;
}
p.bold {
	@include text(var.$font-body, var.$default, var.$SemiBold-weight);
}

p.small {
	@include text(var.$font-body, var.$S, var.$Light-weight);
}

h2.fancy,
h3.fancy,
h4.fancy,
h5.fancy,
h6.fancy,
p.fancy {
	font-family: var.$font-fancy;
}

h1.accent,
h2.accent,
h3.accent,
h4.accent,
h5.accent,
h6.accent,
p.accent {
	color: var.$primary-default;
}
h1.secondary,
h2.secondary,
h3.secondary,
h4.secondary,
h5.secondary,
h6.secondary,
p.secondary {
	opacity: 0.7;
}
h1.center,
h2.center,
h3.center,
h4.center,
h5.center,
h6.center,
p.center {
	text-align: center;
}
h1.left,
h2.left,
h3.left,
h4.left,
h5.left,
h6.left,
p.left {
	text-align: left;
}
h1.right,
h2.right,
h3.right,
h4.right,
h5.right,
h6.right,
p.right {
	text-align: right;
}
h1.white,
h2.white,
h3.white,
h4.white,
h5.white,
h6.white,
p.white {
	color: var.$light;
}
h1.black,
h2.black,
h3.black,
h4.black,
h5.black,
h6.black,
p.black {
	color: var.$dark;
}

h1.faded,
h2.faded,
h3.faded,
h4.faded,
h5.faded,
h6.faded,
p.faded {
	opacity: 0.75;
}
h1.link,
h2.link,
h3.link,
h4.link,
h5.link,
h6.link,
p.link {
	color: var.$link;
}
p.slicedID {
	opacity: 0.6;
}
h1.no-wrap,
h2.no-wrap,
h3.no-wrap,
h4.no-wrap,
h5.no-wrap,
h6.no-wrap,
p.no-wrap {
	white-space: nowrap;
}
// STYLE: LINKS
a {
	@include text(var.$font-body, var.$default, 500);

	color: var.$link;
}
