.brendan {
	margin-bottom: 5rem;

	.section {
		padding: 0 !important;
		box-shadow: 0 0px 80px -20px var.$primary-dark;

		.head-shot {
			border-radius: 50%;
			transform: translateY(-10%);
			border: 12px solid var.$primary-default;
			box-shadow: 5px 0 10px -8px var.$dark;
		}
		h1 {
			text-align: right;
			background-color: var.$primary-default;
			color: var.$text;
			padding: 1rem;
			border-radius: 0.5rem 0.5rem 0 0;
		}
	}
}
@media only screen and (min-width: 0px) and (max-width: var.$screen_M) {
	.brendan {
		padding: 2rem 2em 5rem;
		.section {
			min-height: min-content;
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			grid-template-rows: 5rem auto;
			gap: 1rem;
			.head-shot {
				width: max(8em, 40%);
				grid-row: 1/3;
				grid-column: 1/3;
				z-index: 3;
				transform: translate(-20%, -20%);
			}
			h1 {
				z-index: 1;
				grid-column: 1/4;
				grid-row: 1/2;
			}
			.content {
				grid-area: subtext;
				text-align: right;
				grid-row: 2/3;
				grid-column: 1/4;
				padding: 1rem;
			}
		}
	}
}
@media only screen and (min-width: var.$screen_M) {
	.brendan {
		@include flex-container(row, flex-start, flex-start, wrap);
		gap: 3rem;
		padding: 8rem 0;
		.section {
			width: max(5rem, 60%);
			display: grid;
			grid-template-columns: 2fr 1fr 1fr;
			grid-template-rows: 5rem auto;
			gap: 1rem;
			.head-shot {
				width: min(14rem, 60%);
				grid-row: 1/3;
				grid-column: 1/3;
				z-index: 3;
				transform: translate(-20%, -20%);
			}
			h1 {
				z-index: 1;
				grid-column: 1/4;
				grid-row: 1/2;
			}
			.content {
				grid-area: subtext;
				grid-row: 2/3;
				grid-column: 2/4;
				text-align: right;
				padding-right: 1rem;
				h3 {
					padding: 1rem 0.5rem;
				}
			}
		}
	}
}
