@use "../var";
@import "../mixins";
@import "../typography";

#header {
	position: fixed;
	padding: 1rem 2.5rem;
	z-index: 100;
	top: 0;
	left: 0;
	right: 0;
	background-color: transparent;
	color: var.$text;
	background-color: var.$bg;
	.container {
		display: grid;
		grid-template-areas: "logo title right";
		align-items: center;
		margin: 0 auto;
		max-width: 100rem;

		#logo {
			width: 4rem;
			height: 4rem;
			margin-right: auto;
			grid-area: logo;
			justify-content: left;
		}
		#title {
			grid-area: title;
			color: var.$text;
			margin: 0 auto;
			text-align: center;
		}
		.right {
			grid-area: right;
			@include flex-container(row, flex-end, center, wrap);
			.contact-me {
				text-decoration: none;
				margin-left: auto;
				border-radius: 0.25rem;
				outline: 0 solid var.$primary-dark;
				padding: 0.25rem 0.5rem;
				@include flex-container(row, center, center, nowrap);
				h6 {
					text-decoration: none;

					padding: 0.5rem 1rem;
				}
				.email {
					height: 1.5rem;
				}
				.icon {
					color: var.$text;
					height: 1.25rem;
					opacity: 0;
				}
			}
			.contact-me:hover {
				.icon {
					outline-width: 0;
					opacity: 1;
				}
				outline-width: 2px;
				transition: all 0.6s linear;
			}
			#social-icons {
				display: block;
				@include flex-container(row, flex-end, center, nowrap);
				height: 2rem;

				a {
					color: var.$light;
					margin: 0 0.5rem;
				}
			}
		}
	}
}
@media screen and (min-width: 0px) and (max-width: var.$screen_M) {
	#header {
		.container {
			h1 {
				display: none;
			}
			.right {
				@include flex-container(row, flex-end, center, nowrap);
				// #social-icons {
				// 	display: none;
				// }
				#drawer-icon {
					display: none;
				}
			}
		}
	}
}
@media only screen and (min-width: var.$screen_M) {
	#header {
		.container {
			grid-template-columns: 1fr 2r auto;
			.right {
				#social-icons {
					display: block;
					@include flex-container(row, flex-end, center, nowrap);
					height: 2rem;

					a {
						color: var.$light;
						margin: 0 0.5rem;
					}
				}
				#drawer-icon {
					display: none;
				}
			}
		}
	}
}
