@use "./var";
@import "./mixins";
h1,
h2,
h3,
h4,
h5,
h6,
p {
	padding: 0;
	margin: 0;
	color: #fff;
}

#app-container {
	width: 100vw;
	overflow-x: hidden;
	min-height: 100vh;
	box-sizing: border-box;
	text-align: center;
	overflow-y: scroll;
	background-image: url("../assets/background.png");
	background-size: cover;
	display: grid;
	justify-content: stretch;
	grid-template-columns: 1fr;
	grid-template-rows: 3rem 1fr;
	padding: 0;
	margin: 0 auto;
	grid-template-areas:
		"header header"
		"main main"
		"footer footer";
	#app-header {
		grid-area: header;
		background-color: var.$bg;
	}
	#app-footer {
		padding: 2rem 1rem;
		grid-area: footer;
		background-color: var.$fg;
		text-align: left;
		@include flex-container(row, flex-start, center, wrap);
		gap: 2rem;
	}
	@import "./components/landing_page";
}
.MuiIcon-root,
.MuiSvgIcon-root,
.icon {
	color: "#ccc";
}
.section {
	width: clamp(10rem, 100%, 80rem);
	margin: 0 auto;
	min-height: 6rem;
	border-radius: 0.5rem;
	background: rgba(256, 256, 256, 0.1);
	box-shadow: 0 0px 80px -20px rgba(0, 0, 0, 0.1);

	backdrop-filter: blur(20px);
	-webkit-backdrop-filter: blur(20px);
	padding: 1rem;
	&.red-shadow {
		box-shadow: 0 0px 80px -20px var.$primary-dark;
	}
	&.border {
		border: 3px solid var.$primary-dark;
	}
	&.centered {
		margin: 1rem auto;
	}
	&.hover:hover {
		border: 3px solid var.$border;
		background-color: #303030;
	}
}
.view {
	width: 100vw;
	padding: 5rem;
	background: linear-gradient(
		35deg,
		var.$accent1-dark 5%,
		var.$fg,
		var.$fg,
		var.$primary-dark 95%
	);
}
.page {
	background-color: var.$bg;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;

	.background {
		height: 300vh;
		width: 100%;
	}
	.view {
		height: 100vh;
		.section {
			position: fixed;
			top: 50%;
			left: 50%;
			margin-right: -50%;
			transform: translate(-50%, -50%);
			&.visible {
				opacity: 1;
				transition: opacity 0.5s linear -0.25s;
			}
			&.hidden {
				opacity: 0;
				transition: opacity 0.5s linear -0.25s;
			}
		}
	}
	.centered {
		position: absolute;
	}
	#hello-card {
		position: fixed;
		width: min-content;
	}
}
.card {
	background-color: #262626;
	border: 3px solid #303030;
	padding: 1rem;
	margin: 1rem;
	border-radius: 1rem;
}
.title-card {
	display: grid;
	grid-template-columns: auto 1fr;
	grid-template-rows: 1fr;
	gap: 1.5rem;
}
